/**
 *
 * @param {*} events:
 * The following function should be in the “api.js” file.
 * This function takes an events array, then uses map to create a new array with only locations.
 * It will also remove all duplicates by creating another new array using the spread operator and spreading a Set.
 * The Set will remove all duplicates from the array.
 */

import { mockData } from "./mock-data";
import axios from "axios";
import NProgress from "nprogress";
import "./nprogress.css";

export const extractLocations = (events) => {
	let extractLocations = events.map((event) => event.location);
	let locations = [...new Set(extractLocations)];
	return locations;
};

const checkToken = async (accessToken) => {
	const result = await fetch(`https://www.googleapis.com/oauth2/v1/tokeninfo?access_token=${accessToken}`)
		.then((res) => res.json())
		.catch((error) => error.json());

	return result;
};

export const getEvents = async () => {
	NProgress.start();

	if (window.location.href.startsWith("http://localhost")) {
		NProgress.done();
		return mockData;
	}

	if (!navigator.onLine) {
		const data = localStorage.getItem("lastEvents");
		NProgress.done();
		return data ? JSON.parse(data).events : [];
	}

	const token = await getAccessToken();

	if (token) {
		removeQuery();
		const url = "https://3ojllhvla8.execute-api.us-east-1.amazonaws.com/dev/api/get-events" + "/" + token;
		const result = await axios.get(url);
		if (result.data) {
			var locations = extractLocations(result.data.events);
			localStorage.setItem("lastEvents", JSON.stringify(result.data));
			localStorage.setItem("locations", JSON.stringify(locations));
			// The JSON.stringify(events) function is necessary because events is a list, but localStorage can only store strings.
			// The JSON.stringify() function will convert the list into a string, allowing it to be stored in localStorage.
			// It will then need to be parsed later using the JSON.parse() function when it’s loaded from localStorage. The (stringified) list is stored under the key lastEvents.
		}
		NProgress.done();
		return result.data.events;
	}
};

export const getAccessToken = async () => {
	const accessToken = localStorage.getItem("access_token");
	const tokenCheck = accessToken && (await checkToken(accessToken));

	if (!accessToken || tokenCheck.error) {
		await localStorage.removeItem("access_token");
		const searchParams = new URLSearchParams(window.location.search);
		const code = await searchParams.get("code");
		if (!code) {
			const results = await axios.get("https://3ojllhvla8.execute-api.us-east-1.amazonaws.com/dev/api/get-auth-url");
			const { authUrl } = results.data;
			return (window.location.href = authUrl);
		}
		return code && getToken(code);
	}
	return accessToken;
};

const removeQuery = () => {
	if (window.history.pushState && window.location.pathname) {
		var newurl = window.location.protocol + "//" + window.location.host + window.location.pathname;
		window.history.pushState("", "", newurl);
	} else {
		newurl = window.location.protocol + "//" + window.location.host;
		window.history.pushState("", "", newurl);
	}
};

const getToken = async (code) => {
	const encodeCode = encodeURIComponent(code);
	const { access_token } = await fetch(
		"https://3ojllhvla8.execute-api.us-east-1.amazonaws.com/dev/api/token" + "/" + encodeCode
	)
		.then((res) => {
			return res.json();
		})
		.catch((error) => error);

	access_token && localStorage.setItem("access_token", access_token);

	return access_token;
};
